<template lang="pug">

v-container(fill-height)
  v-layout(row justify-center align-center)
    v-flex(md12 lg10)
      template(v-if="!isGameEnded")
        h2.mb-2          .

        template(v-if="certificate")
          p  :
            strong  {{ certificate.username }}

        p      ,          ,
          |     .
        p       ,     .
        .full-width.mt-3
          template(v-if="isGameStarted")
            v-progress-linear(:value="stagesPercent"  height="25").progress
              .progress-description  {{ Math.ceil(stagesPercent) }}%
            v-btn.ml-0(@click="newGame" color="primary" outline)  
          template(v-if="!isGameEnded")
            v-btn.ml-0(:to="{ name: 'poll' }" color="primary" depressed) {{ isGameStarted ? '' : '' }} 

      template(v-else)
        recommended-lessons.mt-3(
          :result="resultClasses"
          :gender="user.gender"
          @newGame="newGame"
        )
</template>

<script>
import { mapGetters } from 'vuex';

import TheFooter from '@/components/TheFooter';
import RecommendedLessons from '@/components/RecommendedLessons';

import { getGameLength } from '@/game';


const gameLength = getGameLength();

export default {
  name: 'StartPage',
  components: {
    TheFooter,
    RecommendedLessons,
  },
  computed: {
    ...mapGetters({
      currentSection: 'getCurrentSection',
      resultClasses: 'getResultClasses',
      user: 'getUser',
      certificate: 'getCertificate',
    }),
    isGameStarted() {
      return this.currentSection && this.currentSection > 1;
    },
    isGameEnded() {
      return this.currentSection && this.currentSection === gameLength;
    },
    stagesPercent() {
      return (100 / gameLength) * this.currentSection;
    },
  },
  methods: {
    newGame() {
      //    
      this.$store.dispatch('RESET_TEST');
      this.$router.push({ name: 'poll' });
    },
  },
};
</script>

<style scoped lang="scss">
.progress {
  border-radius: 4px;
}
.progress-description {
  color: white;
  text-align: center;
  font-weight: bold;
  height: 100%;
  font-family: Roboto, sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
